import { NextResponse } from 'next/server';
import { cookies } from 'next/headers';
import { jwtVerify } from 'jose';
import pool from '../../../lib/db'; // ‚úÖ ‡πÄ‡∏ä‡πá‡∏Ñ Path ‡πÉ‡∏´‡πâ‡∏ñ‡∏π‡∏Å‡∏ï‡πâ‡∏≠‡∏á‡∏ï‡∏≤‡∏°‡πÇ‡∏Ñ‡∏£‡∏á‡∏™‡∏£‡πâ‡∏≤‡∏á‡πÇ‡∏õ‡∏£‡πÄ‡∏à‡∏Ñ‡∏Ñ‡∏∏‡∏ì

export async function POST(request) {
  const body = await request.json();
  
  // ‚úÖ 1. ‡∏£‡∏±‡∏ö‡∏Ñ‡πà‡∏≤ customerPhone ‡πÄ‡∏û‡∏¥‡πà‡∏°‡πÄ‡∏Ç‡πâ‡∏≤‡∏°‡∏≤
  const { customerName, customerPhone, customerAddress, cart, total } = body;
  
  // 2. ‡∏´‡∏≤ User ID ‡∏Ñ‡∏ô‡∏ó‡∏≥
  const cookieStore = await cookies();
  const token = cookieStore.get('token')?.value;
  if (!token) return NextResponse.json({ error: 'Unauthorized' }, { status: 401 });
  
  const secret = new TextEncoder().encode(process.env.JWT_SECRET);
  const { payload } = await jwtVerify(token, secret);
  const userId = payload.id;

  try {
    // 3. ‡∏™‡∏£‡πâ‡∏≤‡∏á‡πÄ‡∏•‡∏Ç‡∏ó‡∏µ‡πà‡πÉ‡∏ö‡πÄ‡∏™‡∏ô‡∏≠‡∏£‡∏≤‡∏Ñ‡∏≤ (QT-YYYYMM-XXX)
    // ‡πÄ‡∏õ‡∏•‡∏µ‡πà‡∏¢‡∏ô format ‡∏ß‡∏±‡∏ô‡∏ó‡∏µ‡πà‡πÉ‡∏´‡πâ‡πÄ‡∏õ‡πá‡∏ô‡πÅ‡∏ö‡∏ö‡πÑ‡∏ó‡∏¢‡πÜ ‡∏´‡∏£‡∏∑‡∏≠‡πÅ‡∏ö‡∏ö‡∏ó‡∏µ‡πà‡∏ï‡πâ‡∏≠‡∏á‡∏Å‡∏≤‡∏£ (‡∏ï‡∏±‡∏ß‡∏≠‡∏¢‡πà‡∏≤‡∏á‡∏ô‡∏µ‡πâ‡πÉ‡∏ä‡πâ ‡∏õ‡∏µ‡πÄ‡∏î‡∏∑‡∏≠‡∏ô-‡∏™‡∏∏‡πà‡∏°)
    const dateStr = new Date().toISOString().slice(2, 7).replace(/-/g, ''); // 2601 (‡∏™‡∏°‡∏°‡∏ï‡∏¥‡∏õ‡∏µ 2026)
    const random = Math.floor(1000 + Math.random() * 9000);
    const quotationNo = `QT-${dateStr}-${random}`;

    // ‚úÖ 4. Insert ‡∏´‡∏±‡∏ß‡∏ö‡∏¥‡∏• (‡πÄ‡∏û‡∏¥‡πà‡∏° customer_phone)
    const [headerResult] = await pool.query(
      `INSERT INTO quotations (
          quotation_no, 
          customer_name, 
          customer_phone, 
          customer_address, 
          total_amount, 
          created_by
       ) 
       VALUES (?, ?, ?, ?, ?, ?)`,
      [quotationNo, customerName, customerPhone, customerAddress, total, userId]
    );
    const quotationId = headerResult.insertId;

    // 5. Insert ‡∏£‡∏≤‡∏¢‡∏Å‡∏≤‡∏£‡∏™‡∏¥‡∏ô‡∏Ñ‡πâ‡∏≤ (Loop)
    for (const item of cart) {
      await pool.query(
        `INSERT INTO quotation_items (quotation_id, product_id, product_name, price, quantity, total) 
         VALUES (?, ?, ?, ?, ?, ?)`,
        [
          quotationId, 
          item.id, 
          item.name, 
          item.price, 
          item.qty,  // ‡∏à‡∏≥‡∏ô‡∏ß‡∏ô‡∏ó‡∏µ‡πà‡∏•‡∏π‡∏Å‡∏Ñ‡πâ‡∏≤‡πÄ‡∏•‡∏∑‡∏≠‡∏Å
          item.price * item.qty // ‡∏£‡∏≤‡∏Ñ‡∏≤‡∏£‡∏ß‡∏°‡∏Ç‡∏≠‡∏á‡∏£‡∏≤‡∏¢‡∏Å‡∏≤‡∏£‡∏ô‡∏±‡πâ‡∏ô
        ]
      );
    }

    return NextResponse.json({ success: true, quotationId, quotationNo });

  } catch (error) {
    console.error("Database Error:", error);
    return NextResponse.json({ error: 'Database Error' }, { status: 500 });
  }
}